













































import { Vue, Component, Prop, Mixins } from "vue-property-decorator";

export interface ISpeedDialItem {
  to?: string;
  click?: () => void;
  icon: string;
  name: string;
}

@Component({})
export default class SpeedDial extends Mixins() {
  fab = false;

  @Prop({
    type: Array,
    default: () => []
  })
  items!: ISpeedDialItem[];

  onItemClick(item: ISpeedDialItem) {
    if (item.click) {
      item.click();
    }
  }
}
