














import JobOverview from "@/components/jobs/JobOverview.vue";
import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { LoadingScreen } from "@/mixins/loadingScreen";
import { routerModule, jobsModule, repositoriesModule } from "@/store/modules";
import RepositoriesList from "@/components/repositories/RepositoriesList.vue";
import SpeedDial, { ISpeedDialItem } from "@/components/misc/SpeedDial.vue";

@Component({
  components: {
    JobOverview,
    RepositoriesList,
    SpeedDial
  }
})
export default class JobView extends Mixins(LoadingScreen) {
  fab = false;

  get jobId(): string {
    return routerModule.params.jobId;
  }

  get jobData() {
    return jobsModule.getById(this.jobId);
  }

  mounted() {
    this.setLoading(true);
    setTimeout(() => this.setLoading(false), 500);
  }

  async deleteJob() {
    await jobsModule.deleteJob(Number(this.jobId));
    this.$router.replace("/jobs");
  }

  get relatedRepository() {
    if (!this.jobData) return [];
    const repo = repositoriesModule.getById(this.jobData.repoId);
    if (repo) return [repo];
    return [];
  }

  executeJobManually() {
    jobsModule.executeJob(Number(this.jobId));
  }

  get speedDialItems(): ISpeedDialItem[] {
    return [
      {
        to: `/jobs/${this.jobId}/logs`,
        icon: "mdi-file-document-outline",
        name: this.$t("job.show-logs").toString()
      },
      {
        click: this.executeJobManually,
        icon: "mdi-console-line",
        name: this.$t("job.execute-manually").toString()
      },
      {
        to: `/jobs/${this.jobId}/edit`,
        icon: "mdi-pencil",
        name: this.$t("job.edit-job").toString()
      },

      {
        click: this.deleteJob,
        icon: "mdi-delete",
        name: this.$t("job.delete-job").toString()
      }
    ];
  }
}
